// WebSocket connection handler
export const connectWebSocket = async (retries = 3) => {
    for (let i = 0; i < retries; i++) {
        try {
            const sKey = await AsyncStorage.getItem('S-KEY');
            if (!sKey) {
                throw new Error('No S-KEY available');
            }

            return new Promise<void>((resolve, reject) => {
                const authTimeout = setTimeout(() => {
                    webSocketService.removeMessageHandler('authorization');
                    reject(new Error('Authorization timeout'));
                }, 5000);

                webSocketService.addMessageHandler('authorization', (data: WebSocketMessage) => {
                    clearTimeout(authTimeout);
                    if (data.status === 'success') {
                        console.log('WebSocket authorized successfully');
                        resolve();
                    } else {
                        console.error('WebSocket authorization failed:', data);
                        reject(new Error(data.message || 'Authorization failed'));
                    }
                });

                webSocketService.send({
                    type: 'authorization',
                    action: 'connect',
                    S_KEY: sKey
                });
            });
        } catch (error: unknown) {
            console.error(`WebSocket connection attempt ${i + 1} failed:`, error);
            if (i === retries - 1) throw error;
            await new Promise(resolve => setTimeout(resolve, 1000 * (i + 1))); //  
        }
    }
    throw new Error('Failed to connect after retries');
};

// Add interceptor to include S-KEY in requests
authApi.interceptors.request.use(async (config) => {
  const sKey = await AsyncStorage.getItem('S-KEY');
  if (sKey) {
    config.headers['S-KEY'] = sKey;
  }
  return config;
});

// Add response error handling
authApi.interceptors.response.use(
  (response) => response,
  async (error) => {
    console.error('API Error:', error?.response?.data || error.message);
    if (error?.response?.status === 401) {
      // Handle unauthorized error (invalid or expired S-KEY)
      await AsyncStorage.removeItem('S-KEY');
      // You might want to trigger a logout here
    }
    return Promise.reject(error);
  }
);

